@import url('https://fonts.googleapis.com/css?family=Raleway:400,500');
* {
  margin: 0;
  padding: 0;
  list-style: none;
  vertical-align: baseline;
}

div {
  position: relative;
  z-index: 2;
}

body {
  background-color: #ffffff;
  color: #333;
  font-family: 'Raleway', sans-serif;
  font-weight: 400;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.footer {
  width: 450px;
  text-align: center;
  display: block;
  margin: 15px auto 30px auto;
  font-size: 0.8em;
  color: #000;

}
.footer a {
  font-weight: 500;
  text-decoration: none;
  color: #004;
}
#quote-box {
  border-radius: 3px;
  position: relative;
  width: 450px;
  padding: 40px 50px;
  display: table;
  background-color: #aaa;
}
.quote-text {

text-align: center;
width: 450px;
height: auto;
clear: both;
font-weight: 500;
font-size: 1.75em;
}
i {
  font-size: 1em;
  margin-right: 0.4em;
}
.quote-author {
  width: 450px;
  height: auto;
  clear: both;
  padding-top: 20px;
  font-size: 1em;
  text-align: right;
}
.buttons {
  width: 450px;
  margin: auto;
  display: block;
}
.button {
  height: 38px;
  border: none;
  border-radius: 3px;
  color: #000;
  background-color: #fff;
  outline: none;
  font-size: 0.85em;
  padding: 8px 18px 6px 18px;
  margin-top: 30px;
    align-content: center;
  opacity: 1;
  cursor: pointer;
}
.button:hover {
   opacity: 0.9;
 }
#tweet-quote, #tumblr-quote {
   float: left;
   padding: 0px;
   padding-top: 8px;
   text-align: center;
   font-size: 1.2em;
   margin-right: 5px;
   height: 30px;
   width: 40px;
 }
#new-quote {
   float: right;
 }

